body {
  margin: 0px;
  padding: 0px;
  font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
}

a {
  color: #283e4a;
}

body nav {
  background-color: red;
  height: 30px;
  padding: 10px;
}

body nav ul li {
  display: inline-block;
}

body nav ul li a {
  text-decoration: none;
  color: #283e4a;
  display: block;
  background-color: #fff;
  width: 100px;
  text-align: center;
  height: 20px;
  margin-right: 10px;
}

body .container {
  padding: 10px;
  min-height: 450px;
}

form label {
  width: 60px;
  display: inline-block;
}

form fieldset {
  margin-top: 20px;
}

form .campos-form {
  margin-top: 20px;
  margin-bottom: 20px;
}

form input[type=text] {
  border: 1px solid #ccc;
  border-radius: 2px;
  width: 80%;
  height: 30px;
  margin-bottom: 4px;
}

input[type=button], input[type=submit], button {
  border: 1px solid #000;
  border-radius: 5px;
  width: 100px;
  background-color: #dcdcdc;
  color: black;
}

input[type=submit] {
  background-color: #7cc3f5;
}

table {
  width: 100%;
  margin-top: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

table tr td {
  border: 1px solid #ccc;
  padding: 5px;
  text-align: center;
}

table tr td.alterar, table tr td.excluir {
  width: 120px;
}

table tr:nth-child(1) {
  background-color: #ccc;
}

body footer {
  background-color: #283e4a;
  height: 30px;
  padding: 10px;
  color: #fff;
  text-decoration: none;
}